@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1{font-family:'Open Sans Condensed', sans-serif}.container{max-width:1600px;width:100%;margin:0 auto;padding:0 15px}.container-fluid{width:100%;padding:0 15px}.row{margin:0 -15px}.btn{display:inline-block;padding:10px 25px;background-color:#2196f3;color:#f7f7f7;font-weight:bold;border:2px solid #2196f3;text-transform:uppercase;-webkit-transition:.3s ease-in;transition:.3s ease-in;letter-spacing:.5px}.btn:hover{cursor:pointer;color:#f7f7f7;background-color:#0c7cd5;border:2px solid #2196f3}.btn:focus{outline:none}.btn-green{background-color:#4caf50;border:2px solid #4caf50;color:#f7f7f7}.btn-green:hover{background-color:#3d8b40;border:2px solid #4caf50}.btn-delete{background-color:#f44336;color:#f7f7f7;border:1px solid #f7f7f7;-webkit-transition:.3s ease-in;transition:.3s ease-in}.btn-delete:hover{cursor:pointer;background-color:#f7f7f7;color:#f44336;border:1px solid #505050}.form-control{position:relative;margin-bottom:15px}.form-control::after{content:'';display:block;width:100%;height:1px;margin-bottom:30px;background-color:#bdbdbd}.form-control>span{display:block;margin-bottom:7px;font-size:18px;font-weight:300}.form-control input{display:block;width:100%;padding:7px 12px;border:1px solid #bdbdbd;-webkit-transition:.3s ease-in;transition:.3s ease-in;border-radius:3px;margin-bottom:15px;height:33px}.form-control input:focus{border:2px solid #2196f3;outline:none}.form-select{margin-bottom:1em}.form-btn-wrap{display:flex;flex-direction:column;align-items:center}.form-btn{margin-bottom:15px}body{background-color:#f7f7f7}body *{color:#505050;box-sizing:border-box}.centered{display:block;text-align:center}.tooltip{position:relative;display:block;width:27px;height:27px;padding:5px;border:2px solid #bdbdbd;-webkit-transition:.3s ease-in;transition:.3s ease-in;border-radius:50%}.tooltip span{position:absolute;top:50%;left:50%;color:#bdbdbd;line-height:1;-webkit-transform:translate(-50%, -53%);transform:translate(-50%, -53%);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;font-size:12px;-webkit-transition:.3s ease-in;transition:.3s ease-in}.tooltip:hover{border-color:#505050}.tooltip:hover span{color:#505050}.color-green{color:#4caf50}.color-red{color:#f44336}.header{margin-bottom:30px}.navigation{display:flex;justify-content:center;margin:30px 0}.navigation__link{margin:0 15px;padding:7px}

.tracker{max-width:360px;margin:0 auto;padding:20px 30px;border:1px solid #bdbdbd;border-radius:10px;background-color:#f7f7f7}.tracker__title{display:block;margin-bottom:30px;text-align:center}.tracker__display{display:flex;flex:1 1}.tracker__display-title{margin-bottom:30px}.tracker__display-title::after{content:'';display:block;width:100%;height:2px;background-color:#2196f3}.tracker__display-title-text{margin:0}.tracker__display-title-number{margin:0;line-height:1;margin:10px 0}.tracker__display-item{position:relative;display:flex;justify-content:center;align-items:center;flex-direction:column;width:50%;padding:0 15px}.tracker__display-item::after{content:'';display:block;position:absolute;right:0;top:0;height:100%;width:1px;background-color:#f0f0f0}.tracker__display-item:last-of-type::after{display:none}.tracker__display-item-title{display:flex;font-weight:bold;align-items:center;line-height:1;min-height:30px}.tracker__display-item-title .tooltip{margin-left:10px}.tracker-history__title{text-align:center}.tracker-history__list{list-style-type:none;padding:0 17%}.tracker-history__list-item{display:flex;flex-direction:row-reverse;justify-content:flex-end}.tracker-history__list-item-btn{margin-right:5px}.tracker-history__list-item-text{display:flex;width:100%}.tracker-history__list-item-text span{margin-left:auto}.tracker-add-trans{padding:10px 0}.tracker-add-trans__form-item{display:none}

